<template>
  <div class="admin-container">
    <header class="admin-header">
      <svg xmlns="http://www.w3.org/2000/svg" width="48" height="48" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round" class="header-icon"><path d="M12 2a10 10 0 1 0 10 10A10 10 0 0 0 12 2z"></path><path d="M12 18a6 6 0 1 0 0-12 6 6 0 0 0 0 12z"></path><path d="M12 15a3 3 0 1 0 0-6 3 3 0 0 0 0 6z"></path></svg>
      <h1>管理员健康管理中心</h1>
      <p>在此页面统一录入和查询用户的健康数据</p>
    </header>

    <div class="card">
      <h2 class="card-title">
        <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"><path d="M11 4H4a2 2 0 0 0-2 2v14a2 2 0 0 0 2 2h14a2 2 0 0 0 2-2v-7"></path><path d="M18.5 2.5a2.121 2.121 0 0 1 3 3L12 15l-4 1 1-4 9.5-9.5z"></path></svg>
        录入健康数据
      </h2>
      <form @submit.prevent="submitHealthData" class="form-grid">
        <div class="form-group full-width">
          <label for="recordUserId">用户ID (必填)</label>
          <input type="number" id="recordUserId" v-model.number="recordFormData.id" required min="1" placeholder="请输入要录入数据的用户ID">
        </div>
        <div class="form-group">
          <label for="heartRate">心率 (次/分)</label>
          <input type="number" id="heartRate" v-model.number="recordFormData.heartRate" placeholder="例如: 75">
        </div>
        <div class="form-group">
          <label for="bloodPressure">血压 (收缩压/舒张压)</label>
          <input type="text" id="bloodPressure" v-model="recordFormData.bloodPressure" placeholder="例如: 120/80">
        </div>
        <div class="form-group">
          <label for="bloodSugar">血糖 (mmol/L)</label>
          <input type="number" step="0.1" id="bloodSugar" v-model.number="recordFormData.bloodSugar" placeholder="例如: 5.8">
        </div>
        <div class="form-group">
          <label for="weight">体重 (kg)</label>
          <input type="number" step="0.1" id="weight" v-model.number="recordFormData.weight" placeholder="例如: 65.5">
        </div>
        <div class="form-group full-width">
          <label for="medicalHistory">病史</label>
          <input type="text" id="medicalHistory" v-model="recordFormData.medicalHistory" placeholder="输入用户的既往病史，如高血压、糖尿病等">
        </div>
        <div class="form-group full-width">
          <label for="allergyHistory">过敏史</label>
          <input type="text" id="allergyHistory" v-model="recordFormData.allergyHistory" placeholder="输入用户的过敏史，如青霉素过敏、花粉过敏等">
        </div>
        <div class="form-group full-width">
          <button type="submit" class="btn btn-primary">提交数据</button>
        </div>
      </form>
      <div v-if="recordMessage" :class="['message', recordMessageType]">{{ recordMessage }}</div>
    </div>

    <div class="card">
      <h2 class="card-title">
        <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"><path d="M14 2H6a2 2 0 0 0-2 2v16a2 2 0 0 0 2 2h12a2 2 0 0 0 2-2V8z"></path><polyline points="14 2 14 8 20 8"></polyline><line x1="16" y1="13" x2="8" y2="13"></line><line x1="16" y1="17" x2="8" y2="17"></line><polyline points="10 9 9 9 8 9"></polyline></svg>
        查询健康历史数据
      </h2>
      <div class="query-controls">
        <div class="form-group">
          <label for="queryUserId">用户ID (必填)</label>
          <input type="number" id="queryUserId" v-model.number="queryUserId" required min="1" placeholder="请输入要查询的用户ID">
        </div>
        <button @click="fetchHealthHistory" class="btn btn-secondary" :disabled="loading">
          <span v-if="loading" class="spinner"></span>
          {{ loading ? '查询中...' : '查询历史' }}
        </button>
      </div>

      <div v-if="queryMessage && !loading" :class="['message', queryMessageType]">{{ queryMessage }}</div>
      
      <div v-if="searchAttempted && !loading && queryMessageType === 'success'" class="results-grid">
        <div class="result-card text-card">
            <h3>病史</h3>
            <p>{{ queryResults.medicalHistory || '无记录' }}</p>
        </div>
        <div class="result-card text-card">
            <h3>过敏史</h3>
            <p>{{ queryResults.allergyHistory || '无记录' }}</p>
        </div>
        <div class="result-card list-card">
            <h3>心率历史 (次/分)</h3>
            <ul v-if="queryResults.heartRate && queryResults.heartRate.length">
                <li v-for="(item, index) in queryResults.heartRate" :key="index">{{ item }}</li>
            </ul>
            <p v-else class="no-data">无数据</p>
        </div>
        <div class="result-card list-card">
            <h3>血压历史 (收缩/舒张)</h3>
            <ul v-if="queryResults.bloodPressure && queryResults.bloodPressure.length">
                <li v-for="(item, index) in queryResults.bloodPressure" :key="index">{{ item.systolic }}/{{ item.diastolic }}</li>
            </ul>
            <p v-else class="no-data">无数据</p>
        </div>
        <div class="result-card list-card">
            <h3>血糖历史 (mmol/L)</h3>
            <ul v-if="queryResults.bloodSugar && queryResults.bloodSugar.length">
                <li v-for="(item, index) in queryResults.bloodSugar" :key="index">{{ item }}</li>
            </ul>
            <p v-else class="no-data">无数据</p>
        </div>
        <div class="result-card list-card">
            <h3>体重历史 (kg)</h3>
            <ul v-if="queryResults.weight && queryResults.weight.length">
                <li v-for="(item, index) in queryResults.weight" :key="index">{{ item }}</li>
            </ul>
            <p v-else class="no-data">无数据</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import config from '@/utils/config';

export default {
  name: 'HealthManagementAdmin',
  data() {
    return {
      // 使用全局配置的API地址
BASE_URL: config.API_BASE_URL,
      recordFormData: {
        id: null,
        medicalHistory: '',
        allergyHistory: '',
        heartRate: null,
        bloodPressure: '',
        bloodSugar: null,
        weight: null,
      },
      recordMessage: '',
      recordMessageType: '',
      queryUserId: null,
      loading: false,
      queryMessage: '',
      queryMessageType: '',
      searchAttempted: false, // 标记是否已执行过查询
      queryResults: { // 存储所有查询结果
        heartRate: [],
        bloodPressure: [],
        bloodSugar: [],
        weight: [],
        medicalHistory: '',
        allergyHistory: '',
      },
    };
  },
  methods: {
    getAuthToken() {
      // 实际应用中，应从安全位置（如 Vuex store 或 HttpOnly cookie）获取
      // 此处为示例，直接从 localStorage 获取
      return localStorage.getItem('adminToken');
    },

    async submitHealthData() {
      this.recordMessage = '';
      if (!this.recordFormData.id) {
        this.recordMessage = '用户ID是必填项！';
        this.recordMessageType = 'error';
        return;
      }
      
      const token = this.getAuthToken();
      if (!token) {
        this.recordMessage = '认证失败，请以管理员身份登录。';
        this.recordMessageType = 'error';
        return;
      }
      
      const dataToSend = {};
      for (const key in this.recordFormData) {
        if (this.recordFormData[key] !== null && this.recordFormData[key] !== '' && !Number.isNaN(this.recordFormData[key])) {
          dataToSend[key] = this.recordFormData[key];
        }
      }

      try {
        const response = await fetch(`${this.BASE_URL}/health/record`, {
          method: 'POST',
          headers: { 'Content-Type': 'application/json', 'Authorization': `Bearer ${token}` },
          body: JSON.stringify(dataToSend),
        });
        const result = await response.json();
        if (response.ok) {
          this.recordMessage = '健康数据录入成功！';
          this.recordMessageType = 'success';
          // 保留用户ID以便连续录入
          this.recordFormData = { id: this.recordFormData.id, medicalHistory: '', allergyHistory: '', heartRate: null, bloodPressure: '', bloodSugar: null, weight: null };
        } else {
          this.recordMessage = `录入失败: ${result.msg || response.statusText}`;
          this.recordMessageType = 'error';
        }
      } catch (error) {
        this.recordMessage = '网络请求失败，请检查后端服务是否正常。';
        this.recordMessageType = 'error';
      }
    },

    async fetchHealthHistory() {
      this.searchAttempted = true; // 标记已尝试查询
      this.queryMessage = '';
      this.queryResults = { heartRate: [], bloodPressure: [], bloodSugar: [], weight: [], medicalHistory: '', allergyHistory: '' };

      if (!this.queryUserId) {
        this.queryMessage = '请输入要查询的用户ID！';
        this.queryMessageType = 'error';
        return;
      }
      const token = this.getAuthToken();
      if (!token) {
        this.queryMessage = '认证失败，请以管理员身份登录。';
        this.queryMessageType = 'error';
        return;
      }

      this.loading = true;
      const metrics = ['heartRate', 'bloodPressure', 'bloodSugar', 'weight', 'medicalHistory', 'allergyHistory'];
      
      try {
        const requests = metrics.map(metric => {
            const url = `${this.BASE_URL}/health/${metric}/history?id=${this.queryUserId}`;
            return fetch(url, {
                headers: { 'Authorization': `Bearer ${token}` }
            }).then(async response => {
                if (!response.ok) {
                    const errorData = await response.json().catch(() => ({ msg: response.statusText }));
                    throw new Error(`用户(ID: ${this.queryUserId})的'${metric}'数据查询失败: ${errorData.msg}`);
                }
                const contentType = response.headers.get("content-type");
                return contentType && contentType.includes("application/json") ? response.json() : response.text();
            });
        });

        const responses = await Promise.allSettled(requests);
        
        const successfulResponses = responses.filter(r => r.status === 'fulfilled');
        
        if (successfulResponses.length === 0) {
            const firstError = responses.find(r => r.status === 'rejected');
            throw new Error(firstError.reason.message || '所有数据请求均失败');
        }

        const [heartRate, bloodPressure, bloodSugar, weight, medicalHistory, allergyHistory] = responses.map(r => r.status === 'fulfilled' ? r.value : (Array.isArray(this.queryResults[metrics[responses.indexOf(r)]]) ? [] : ''));

        this.queryResults = { heartRate, bloodPressure, bloodSugar, weight, medicalHistory, allergyHistory };
        this.queryMessage = '查询成功！';
        this.queryMessageType = 'success';

      } catch (error) {
        this.queryMessage = `查询失败: ${error.message}`;
        this.queryMessageType = 'error';
        console.error('查询健康历史数据出错:', error);
      } finally {
        this.loading = false;
      }
    },
  },
};
</script>

<style>
:root {
  --primary-color: #4A90E2; /* A modern, friendly blue */
  --secondary-color: #50E3C2; /* A fresh, vibrant teal/green */
  --bg-color: #F7F9FC;
  --card-bg: #FFFFFF;
  --text-primary: #2c3e50;
  --text-secondary: #5a6b7e;
  --border-color: #E6EAF0;
  --success-bg: #E4F8F0;
  --success-text: #008248;
  --error-bg: #FDEDEE;
  --error-text: #D93025;
  --input-bg: #FFFFFF;
}

.admin-container {
  max-width: 1200px;
  margin: 40px auto;
  padding: 20px;
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
  background-color: var(--bg-color);
  color: var(--text-primary);
}

.admin-header {
  text-align: center;
  margin-bottom: 3rem;
}
.header-icon {
  color: var(--primary-color);
  margin-bottom: 0.5rem;
}
.admin-header h1 {
  font-size: 2.8rem;
  font-weight: 700;
  margin: 0;
}
.admin-header p {
  font-size: 1.2rem;
  color: var(--text-secondary);
  margin-top: 0.5rem;
}

.card {
  background-color: var(--card-bg);
  border-radius: 16px;
  padding: 2.5rem;
  margin-bottom: 2.5rem;
  box-shadow: 0 10px 30px -15px rgba(100, 116, 139, 0.15);
  border: 1px solid var(--border-color);
}

.card-title {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  color: var(--text-primary);
  font-size: 1.75rem;
  font-weight: 600;
  margin-top: 0;
  margin-bottom: 2rem;
  padding-bottom: 1rem;
  border-bottom: 1px solid var(--border-color);
}
.card-title svg {
  color: var(--primary-color);
}


.form-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1.5rem 2rem;
}
.form-group.full-width {
  grid-column: 1 / -1;
}

.form-group label {
  display: block;
  margin-bottom: 0.6rem;
  color: var(--text-secondary);
  font-weight: 500;
  font-size: 0.95rem;
}

.form-group input {
  width: 100%;
  padding: 14px 16px;
  box-sizing: border-box;
  background-color: var(--input-bg);
  border: 1px solid var(--border-color);
  border-radius: 10px;
  font-size: 1rem;
  font-family: inherit;
  transition: border-color 0.2s, box-shadow 0.2s;
}
.form-group input::placeholder {
    color: #a0aec0;
}
.form-group input:focus {
  outline: none;
  border-color: var(--primary-color);
  box-shadow: 0 0 0 3px rgba(74, 144, 226, 0.2);
}

.query-controls {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-end;
  gap: 1.5rem;
  margin-bottom: 2rem;
}
.query-controls .form-group {
  flex-grow: 1;
  margin-bottom: 0;
}
.query-controls .btn {
    flex-shrink: 0;
    min-width: 140px;
}

.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  padding: 14px 28px;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  font-size: 1rem;
  font-weight: 600;
  transition: all 0.25s ease;
  width: auto; /* Allow button to size to content */
}
.form-group .btn {
    width: 100%; /* Full width inside form */
}

.btn-primary {
  background-color: var(--primary-color);
  color: white;
}
.btn-primary:hover:not(:disabled) {
  background-color: #357ABD;
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(74, 144, 226, 0.2);
}
.btn-secondary {
  background-color: var(--secondary-color);
  color: white;
}
.btn-secondary:hover:not(:disabled) {
  background-color: #45D1B5;
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(80, 227, 194, 0.3);
}
.btn:disabled {
  background-color: #E9ECEF;
  color: #a0aec0;
  cursor: not-allowed;
}

.message {
  padding: 1rem 1.5rem;
  margin-top: 2rem;
  border-radius: 10px;
  text-align: center;
  font-weight: 500;
  border-width: 1px;
  border-style: solid;
}
.message.success {
  background-color: var(--success-bg);
  color: var(--success-text);
  border-color: var(--success-text);
}
.message.error {
  background-color: var(--error-bg);
  color: var(--error-text);
  border-color: var(--error-text);
}

.results-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 1.5rem;
  margin-top: 2rem;
}
.result-card {
  background-color: #F7F9FC;
  border: 1px solid var(--border-color);
  border-radius: 12px;
  padding: 1.5rem;
  display: flex;
  flex-direction: column;
}
.result-card h3 {
  margin-top: 0;
  margin-bottom: 1rem;
  font-size: 1.2rem;
  font-weight: 600;
  color: var(--text-primary);
  border-bottom: 1px solid var(--border-color);
  padding-bottom: 0.75rem;
}
.result-card.text-card p {
  font-size: 1rem;
  color: var(--text-secondary);
  line-height: 1.6;
  white-space: pre-wrap; /* Preserve line breaks if any */
}
.result-card.list-card {
  min-height: 250px; /* Ensure consistent height */
}
.result-card.list-card ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
  flex-grow: 1;
  max-height: 200px;
  overflow-y: auto;
  /* Custom scrollbar */
  scrollbar-width: thin;
  scrollbar-color: var(--primary-color) var(--border-color);
}
.result-card.list-card ul::-webkit-scrollbar {
  width: 8px;
}
.result-card.list-card ul::-webkit-scrollbar-track {
  background: var(--border-color);
  border-radius: 10px;
}
.result-card.list-card ul::-webkit-scrollbar-thumb {
  background-color: var(--primary-color);
  border-radius: 10px;
  border: 2px solid var(--border-color);
}

.result-card.list-card li {
  padding: 10px 12px;
  border-bottom: 1px solid #E6EAF0;
  font-size: 0.95rem;
  color: var(--text-secondary);
  display: flex;
  justify-content: space-between;
}
.result-card.list-card li:last-child {
  border-bottom: none;
}
.result-card .no-data {
  color: #999;
  font-style: italic;
  text-align: center;
  margin: auto;
}

/* Spinner for loading button */
.spinner {
  width: 16px;
  height: 16px;
  border: 2px solid rgba(255, 255, 255, 0.3);
  border-radius: 50%;
  border-top-color: #fff;
  animation: spin 1s ease-in-out infinite;
}
@keyframes spin {
  to { transform: rotate(360deg); }
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .form-grid {
    grid-template-columns: 1fr;
  }
  .query-controls {
    flex-direction: column;
    align-items: stretch;
  }
  .query-controls .btn {
    width: 100%;
  }
  .admin-header h1 {
    font-size: 2.2rem;
  }
  .card {
    padding: 1.5rem;
  }
}
</style>
